a.popupvideo figure {
  width: 100%;
  position: relative;
  margin-bottom: 20px; }
  a.popupvideo figure > img {
    width: 100%;
    display: block; }
  a.popupvideo figure:after {
    -moz-transform: scale(1);
    transform: scale(1);
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    moz-transition: all 0.2s ease-in-out;
    o-transition: all 0.2s ease-in-out;
    ms-transition: all 0.2s ease-in-out;
    content: "\f144";
    font-family: 'icon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 102.5px;
    height: 102.5px;
    color: #d95e6b;
    font-size: 102.5px;
    position: absolute;
    top: 50%;
    margin-top: -21.25px;
    left: 50%;
    margin-left: -51.25px;
    border-radius: 50%; }
  a.popupvideo figure:hover:after {
    box-shadow: 0 12px 57px rgba(217, 94, 107, 0.71);
    -webkit-box-shadow: 0 12px 57px rgba(217, 94, 107, 0.71);
    -moz-box-shadow: 0 12px 57px rgba(217, 94, 107, 0.71);
    -o-box-shadow: 0 12px 57px rgba(217, 94, 107, 0.71);
    -ms-box-shadow: 0 12px 57px rgba(217, 94, 107, 0.71);
    -moz-transform: scale(1.5);
    transform: scale(1.5);
    -webkit-transform: scale(1.5);
    -ms-transform: scale(1.5); }
  a.popupvideo figure figcaption {
    color: #fff;
    font-size: 12px;
    font-weight: 400;
    position: absolute;
    width: 100%;
    text-align: center;
    left: 0;
    bottom: 80px; }
